@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}
/*--------------------*/

body {
  background-color: #080710;
}
.background {
  width: 430px;
  height: 520px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}
.background .shape {
  height: 200px;
  width: 200px;
  position: absolute;
  border-radius: 50%;
}
.shape:first-child {
  background: linear-gradient(#1845ad, #23a2f6);
  left: -80px;
  top: -80px;
}
.shape:last-child {
  background: linear-gradient(to right, #ff512f, #f09819);
  right: -30px;
  bottom: -80px;
}
form {
  height: 520px;
  width: 400px;
  background-color: rgba(255, 255, 255, 0.13);
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border-radius: 10px;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
  padding: 50px 35px;
}
form * {
  font-family: 'Poppins', sans-serif;
  color: #ffffff;
  letter-spacing: 0.5px;
  outline: none;
  border: none;
}
form h3 {
  font-size: 32px;
  font-weight: 500;
  line-height: 42px;
  text-align: center;
}

label {
  display: block;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 500;
}
input {
  display: block;
  height: 50px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.07);
  border-radius: 3px;
  padding: 0 10px;
  margin-top: 8px;
  font-size: 14px;
  font-weight: 300;
}
:-ms-input-placeholder {
  color: #e5e5e5;
}
::placeholder {
  color: #e5e5e5;
}
button {
  margin-top: 50px;
  width: 100%;
  background-color: #ffffff;
  color: #080710;
  padding: 15px 0;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
  cursor: pointer;
}

.chat-input {
  width: 100%;
  height: 100px;
  border-radius: 10px;
  padding: 5px;
}

.chat-input:hover {
  border: 2px solid green;
  box-shadow: 0 0 10px rgba(23, 241, 3, 0.5);
}

.image {
  display: flex;
  flex-direction: column;
}

.image img {
  width: 150px;
  height: 120px;
  border-radius: 10px;
}

body {
  height: 100%;
}

.message {
  display: flex;
  margin: 5px 0;
}

.me {
  flex-direction: row-reverse;
}
.emojis {
  padding: 10px;
}

.message-content {
  max-width: 440px;
}

.message-bubble {
  background-color: #3676ff;
  box-shadow: 0px 5px 5px rgba(54, 116, 225, 0.196733);
  border-radius: 12px;
  border: 1px solid #ececec;
  padding: 15px;
  margin-right: 5px;
}
.message-text {
  color: #ffff;
  display: flex;
  align-items: center;
}

.message-text p {
  margin-right: 10px;
  margin-top: 5px;
}
.message-text__me {
  display: flex;
  flex-direction: row-reverse;
  color: #fff;
  margin-right: 10px;
  min-width: 100%;
}

.message-text__me p {
  margin-right: 10px;
  display: flex;
  flex-direction: row-reverse;
}

.description {
  display: flex;
  /* flex-direction: row-reverse; */
}

.isMe {
  background-color: green;
  border: 1px solid #ececec;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.220444);
}

.author {
  color: white;
  font-size: 14px;
  opacity: 0.9;
  margin-left: 10px;
}

.container {
  max-width: 1140px;
  min-height: 100%;
  margin: 0 auto;
}

.chat-header {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  padding: 10px;
  border-bottom: 1px solid #526990;
  background: linear-gradient(45deg, #526990, #853e68);
  border-radius: 10px;
  margin-bottom: 10px;
}

.chat {
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.04204);
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 20px;
  background-image: url(/react-lite-chat-app/static/media/chat-bg.e65de74a.jpg);
  background-size: cover;
  height: calc(450px - (45px + 70px));
  position: relative;
}

.chat-actions {
  display: flex;
  align-items: center;
}

.actions-left {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin-right: 10px;
  background-color: #fff;
  padding: 3px;
  border-radius: 10px;
  min-width: 30px;
  height: 100px;
}

.input-file {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.emoji-list {
  height: 18px;
  width: 18px;
  cursor: pointer;
}

.send-btn {
  height: 50px;
  width: 50px;
  margin-left: 10px;
  background-color: #fff;
  border-radius: 10px;
  padding-left: 5px;
  padding-right: 4px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.send-btn:hover {
  border: 1px solid green;
  box-shadow: 0 0 10px rgba(23, 241, 3, 0.5);
}

.send-btn:active {
  background-color: rgba(23, 241, 3, 0.5);
}

.send-btn img {
  height: 35px;
  width: 35px;
}

.send-file {
  height: 18px;
  width: 18px;
  cursor: pointer;
  display: flex;
  align-items: flex-end;
}

.send-file img {
  height: 18px;
  width: 18px;
  cursor: pointer;
}

.messages {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.messages::-webkit-scrollbar {
  display: none;
}
.header {
  padding-top: 10px;
}

